const Auth = require("./auth.js");

/**
 * request
 */
function request(url, data = {}, method = "GET") {
	return new Promise(function(resolve, reject) {
		uni.showLoading({
			title: ''
		});

		data.token = Auth.getToken();
		
		if (method == "GET") {
			data.t = new Date().getTime();
			data.r = Math.floor(Math.random() * 10000);
		}
		

		data.os = 'wx';

		



		



		
		uni.request({
			url: url,
			data: data,
			method: method,
			success(res) {
				if (res.statusCode == 500 && res.data.code == 'user_not_login') {
					uni.clearStorageSync();
					uni.navigateTo({
					    url: '/pages/user/login/login',
					});
					return;
				}
				
				if (res.statusCode != 200) {
					reject(res.errMsg);
					return;
				}

				if (res.data.code == 'user_not_login') {
					uni.clearStorageSync();
					uni.navigateTo({
					    url: '/pages/user/login/login',
					});
					return;
				} else if (res.data.code == 'require_mobile') {
					uni.navigateTo({
					    url: '/pages/user/login/login?type=mobile',
					});
					return;
				}
				
				resolve(res.data);
			},
			fail(err) {
				reject(err);
			},
			complete() {
				uni.hideLoading();
			}
		});
	});
}

/**
 * 
 */
function upload(url, path, data = {}) {
    return new Promise(function (resolve, reject) {
        uni.showLoading({
            title: '',
        })

        data.token = Auth.getToken();
		

		data.os = 'wx';

		



		



        
        uni.uploadFile({
            url: url,
            filePath: path,
            name: 'image',
            formData: data,
            success(res) {
                if (res.statusCode != 200) {
                    reject(res.errMsg);
                    return;
                }

				let data = undefined;
				if (res.data instanceof String || (typeof res.data).toLowerCase() == 'string') {
					data = JSON.parse(res.data);
				} else {
					data = res.data;
				}
                
                if (data.code == -1) { //
                    uni.navigateTo({
                        url: '/pages/user/login/login',
                    });
                    return;
                }

                resolve(data);
            }, 
			fail(err) {
				console.log(err)
			},
			complete() {
				uni.hideLoading();
			}
        })
    });
}

/**
 * get
 */
function get(url, data = {}) {
	return request(url, data, 'GET');
}

/**
 * post
 */
function post(url, data = {}) {
	return request(url, data, 'POST');
}

module.exports = {
	get,
	post,
	upload,
};
